<template>
  <v-app-bar app color="primary" dark>
    <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>
    <v-toolbar-title class="d-none d-md-flex">My Navbar</v-toolbar-title>
    <v-spacer></v-spacer>
    <v-btn
      text
      v-for="link in links"
      :key="link.text"
      :to="link.href"
      class="d-none d-md-flex"
    >
      {{ link.text }}
    </v-btn>
    <v-menu offset-y v-if="!drawer" class="d-flex d-md-none">
      <template v-slot:activator="{ on, attrs }">
        <v-btn text v-bind="attrs" v-on="on">
          <v-icon>mdi-menu</v-icon>
        </v-btn>
      </template>
      <v-list>
        <v-list-item
          v-for="link in links"
          :key="link.text"
          :to="link.href"
          @click="drawer = false"
        >
          <v-list-item-title>{{ link.text }}</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </v-app-bar>

  <v-navigation-drawer v-model="drawer" app temporary class="d-md-none">
    <v-list>
      <v-list-item
        v-for="link in links"
        :key="link.text"
        :to="link.href"
        @click="drawer = false"
      >
        <v-list-item-title>{{ link.text }}</v-list-item-title>
      </v-list-item>
    </v-list>
  </v-navigation-drawer>
</template>
<script>
import { defineComponent } from "vue";

export default defineComponent({
  name: "HomeView",
  data() {
    return {
      model: null,
      expand: false,
      expand2: false,
      expand3: false,
      loading: false,
      drawer: false,
      links: [
        { text: "Home", href: "/" },
        { text: "About", href: "/about" },
        { text: "Services", href: "/services" },
        { text: "Contact", href: "/contact" },
      ],
    };
  },
  watch: {
    loading(val) {
      if (!val) return;

      setTimeout(() => (this.loading = false), 2000);
    },
  },
});
</script>
