<script>
export default {
    data: () => ({ active: true, value: 0 }),
    computed: {
        color() {
            switch (this.value) {
                case 0:
                    return 'blue-grey'
                case 1:
                    return 'teal'
                case 2:
                    return 'brown'
                default:
                    return 'blue-grey'
            }
        },
    }
}
</script>